import Vue from 'vue'
import Router from 'vue-router'
import WorkList from '@/components/WorkList'
import WorkView from '@/components/WorkView'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'workList',
      component: WorkList
    },
    {
      path: '/category/:category',
      name: 'workListFiltered',
      component: WorkList
    },
    {
      path: '/work/:id',
      name: 'workView',
      component: WorkView
    }
  ]
})



